/* Upcoming Events Styling */
.upcomingEventItem {
    margin: 10px;
}
.upcomingEventItem img {
    height: 150px;
    width: 150px;
}
.upcomingEventItem h3 {
    padding-left: 10px;
}
.upcomingEventItem p {
    padding-left: 10px;
}

.carousel-indicators{
    display:none;
}
/* Previous Events Styling */
.previousEvents {
    margin: auto;
    padding-left: 50px;
    width: 90%;
}
.eventItem {
    padding: 15px;
}
.eventItem img {
    height: 200px;
    width: 200px;
}
.eventItem h3 {
    padding-left: 10px;
}
.eventItem p {
    padding-left: 10px;
}

/* Old Modal Styling */
.modal-dialog {
    max-width: 80%;
}
.modal-body {
    margin: auto;
    padding-left: 70px;
}
.modal-body img {
    height: 250px;
    width: 250px;
    padding: 16px;
}

/* New Modal Styling */
a :hover {
    opacity: 0.5;
}
.moveLeft {
    margin-left: -42px;
}
.col {
    text-align: center;
}
.btnImg {
    height: 250px;
    width: 250px;
}
.spaceToFooter {
    margin-bottom: 20px;
}


